@import url(https://fonts.googleapis.com/css?family=Oswald);
@import url(https://use.fontawesome.com/releases/v5.8.1/css/all.css);
body {
  margin: 0;
  padding: 0;
  font-family: "Oswald", "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  font-weight: normal;  
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.app {
    height: 100vh;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    color: #222;
}
.app__inner {
    border-radius: 4px;
    width: 50%;
    height: 350px;
    overflow: hidden;
    display: -webkit-flex;
    display: flex;
    border: 1px solid #2b75b1;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}

.app__content {
    width: 70%;
    padding: 10px;
}
.history {
    width: 100%;
}

.history a {
    color: #2b75b1;
    margin: 0 2px;
    font-size: 14px;
}
.app__form {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-flex-flow: row wrap;
            flex-flow: row wrap;
}

.app__search {
    font-size: 20px;
    padding: 6px 6px;
    margin-right: 15px;
    font-family: 'Oswald', sans-serif;
    height: 40px;
    box-sizing: border-box;
    width: 279px;
    text-transform: capitalize;
    border:1px solid #2b75b1;

}

.app__submit {
    color: #fff;
    font-size: 20px;
    font-family: 'Oswald', sans-serif;
    background-color: #2b75b1;
    border: none;
    /* padding: 10px; */
    width: 140px;
    outline: none;
    height: 40px;
    box-sizing: border-box;
    cursor: pointer;
}

.app__submit-inactive {
    background-color: #777;
}

.app__submit:not(.app__submit-inactive):hover {
    background-color: #fff;
    color: #2b75b1;
    border: 1px solid #2b75b1;
}
.weather {
    text-align: center;
}

.weather__title {
    text-align: center;
    position: relative;
    margin-bottom: 15px;
    font-size: 26px;
}

.weather__title:after {
    content: '';
    display: block;
    position: absolute;
    left: 0;
    right: 0;
    height: 1px;
    background-color: #2b75b1;
    top: 50px;
}

.weather__condition {
    font-size: 35px;
}

.weather p {
    margin: 0;
    padding: 0;
}

.weather__forecast {
    margin-top: 20px;
    display: inline-block;
}

.weather__forecast:visited {
    color: #2b75b1;
}

.day {
    padding: 5px;
    text-align: center;
}

.day p {
    margin: 5px 0;
}

.day__date {
    font-weight: bold;
}

.forecast {
    text-align: center;
}
.forecast__title {
    position: relative;
    margin-bottom: 15px;
    font-size: 26px;
}

.forecast__title:after {
    content: '';
    display: block;
    position: absolute;
    left: 0;
    right: 0;
    height: 1px;
    background-color: #2b75b1;
    top: 50px;
}

.forecast__days {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}

.forecast__link-back {
    display: inline-block;
    margin-top: 10px;
}

.forecast__link-back:visited {
    color: #2b75b1;
}
.logo {
    background-color: #2b75b1;
    height: 100%;
    width: 30%;
    color:#fff ;
    font-size: 40px;
    text-align: center;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    text-transform: uppercase;
}

.logo__icons i {
    font-size: 25px;
    margin: 0 5px;
}
